import React, { useEffect, useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import SecIntro from "./SecIntro";
import WhyChooseUsIco from "../assets/images/whyChooseUs.svg";
import { Link } from "react-router-dom";
import { apiPost } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";

function WhyChooseUs() {
  const [aboutUsHomePageData, setAboutUsHomePageData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchAboutUsData = async () => {
      try {
        const response = await apiPost(API_ENDPOINTS.ABOUT, { page: "home" });
        
        if (response.success && response.data) {
          setAboutUsHomePageData(response.data);
          setError(null);
        } else {
          throw new Error("Failed to fetch 'Why Choose Us' data.");
        }
      } catch (error) {
        console.error("Error fetching data:", error);
        setError(error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchAboutUsData();
  }, []);

  const contentData = {
    buttonName: "Make a Booking >",
  };

  const introData = {
    titleStart: "Why Choose",
    highlight: "Us",
    titleEnd: "",
    theme: "",
  };

  if (loading) return <p>Loading why choose data...</p>;
  if (error) return <p>Error loading data: {error}</p>;

  return (
    <div className="WhyChooseUs">
      <Container>
        <Row>
          <Col lg={12}>
            <SecIntro
              titleStart={introData.titleStart}
              highlight={introData.highlight}
              titleEnd={introData.titleEnd}
              theme={introData.theme}
            />
            <div className="WhyChooseUs__wrp">
              <div className="WhyChooseUs__left">
                {/* <h4>{aboutUsHomePageData?.title || "Why Choose Us"}</h4> */}
                <p
                  dangerouslySetInnerHTML={{
                    __html: aboutUsHomePageData?.description || "No description available.",
                  }}
                ></p>
                <Link to="/book-now" className="btn btn-blue">
                  {contentData.buttonName}
                </Link>
              </div>
              <div className="WhyChooseUs__right">
                <img src={WhyChooseUsIco} alt="Why Choose Us Icon" />
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default WhyChooseUs;
