import {React, useState, useEffect} from "react";
import { useLocation } from "react-router-dom"; // Import useLocation for receiving state
import { Container, Row, Col } from "react-bootstrap";
import DatePicker from "react-datepicker"; //date -picker
import "react-datepicker/dist/react-datepicker.css";//date picker
import { format } from "date-fns";//date picker
import PageTopBanner from "./PageTopBanner";
import HomeFaq from "./HomeFaq";
import WhyChooseUs from "./WhyChooseUs";
import SearchForm from "./SearchForm";
import SecIntro from "./SecIntro";
import DescriptionBlock from "./DescriptionBlock";
import ProfileCards from "./Instructor/ProfileCards";

function Search() {

  

  const [timeFilter, setTimeFilter] = useState('')
  const [dateFilter, setDateFilter] = useState('')
  const [availableTrainer, setAvailableTrainer] = useState([])
  const location = useLocation(); // Use useLocation to access passed state
  const searchTerm = location.state?.searchTerm || ""; // Retrieve the search term or default to an empty string
  const searchData = location.state?.searchResult || [];
  const searchStatus = location.state?.searchStatus;
  //console.log(searchData, searchStatus);
  const [uniqueTrainer, setUniqueTrainer] = useState([])
  const [noOfDays, setNoOfDays] = useState('7 days')

  const contentData = {
    bottomDesc: [
      "Find, compare and book driving lessons with the best driving instructors near you.",
    ],
  };
  const introData = {
    titleStart: "Your local driving instructors in",
    highlight: searchTerm,
    titleEnd: "",
    theme: "",
  };

//Function to apply both filters
// const applyFilters = (time, date) => {
//     const filteredData = searchData.filter(item => {
//     const timeMatch = time ? (item.time === time || item.time === "Anytime") : true;//show all time if Anytime is present 
//     const dateMatch = date ? item.days === date : true;
//     return timeMatch && dateMatch;
//   });

//  // console.log("Filtered Data:", filteredData); // Log the filtered data
//   setAvailableTrainer(filteredData); // Update the state asynchronously
// };

// const applyFilters = (time, date) => {
//   const filteredData = searchData.filter(item => {
//       let timeMatch;

//       if (time === 'Anytime') {
//           // Only include entries that have both 'AM' and 'PM'
//           timeMatch = item.time.includes('AM') && item.time.includes('PM');
//       } else {
//           // Only include entries that have exactly one value (either 'AM' or 'PM')
//           timeMatch = item.time.length === 1 && item.time.includes(time);
//       }

//       const dateMatch = date ? item.days === date : true;

//       return timeMatch && dateMatch;
//   });

//   setAvailableTrainer(filteredData);
// };


const applyFilters = (time, date) => {
  const filteredData = searchData.filter(item => {
      let timeMatch;

      if (time === 'Anytime') {
          // Show only entries that contain both 'AM' and 'PM'
          timeMatch = item.time.includes('AM') && item.time.includes('PM');
      } else {
          // Show entries that either exactly match AM/PM or include both AM and PM
          timeMatch = item.time.includes(time);
      }

      //const dateMatch = date ? item.days === date : true;
      const dateMatch = date ? item.days.includes(date) : true;

      return timeMatch && dateMatch;
  });

  setAvailableTrainer(filteredData);
};

useEffect(() => {
  const uniqueProfiles = Array.from(
    new Map(availableTrainer.map(profile => [profile.instructor_id, profile])).values()
  );
  setUniqueTrainer(uniqueProfiles)
 // console.log("Updated availableTrainer:", availableTrainer, uniqueTrainer);
}, [availableTrainer]);



const changeTime=(e)=>{
 const timeValue =e.target.value;
 setTimeFilter(timeValue)
 //const filterTime = searchData.filter( filtertime => filtertime.time === timeValue)
//  setAvailableTrainer(filterTime)
  applyFilters(timeValue, dateFilter); // Pass the updated time and current dateFilter
}

const changeDay=(e)=>{
  //console.log(e.target.value)
  const dateValue = e.target.value;
  setDateFilter(e.target.value)
  const filterDate = searchData.filter( filterdate => filterdate.days === dateValue)
 // setAvailableTrainer(filterDate)
  applyFilters(timeFilter, dateValue); // Pass the updated date and current timeFilter
}
// datepicker
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState(null);
  const [dateRange, setDateRange]= useState()
  const onChangeDate = (dates) => {
    const [start, end] = dates;
    // console.log(dates)
    setStartDate(start);
    setEndDate(end);
    
    const formattedStartDate = format(new Date(start), "yyyy-MM-dd");
    const formattedEndDate = end ? format(new Date(end), "yyyy-MM-dd") : null;
    const shortStartDate = format(new Date(start), "dd MMM");
    const shortEndDate = end ? format(new Date(end), "dd MMM") : null;
    setNoOfDays(`${shortStartDate}  ${shortEndDate !== null ? '-' : ''}   ${shortEndDate !== null ? shortEndDate : ''}`)//for display the date range in heading message
   // console.log(getDatesInRange(formattedStartDate, formattedEndDate));
    const calculatedRange = getDatesInRange(formattedStartDate, formattedEndDate);

    // Set the calculated range
    setDateRange(calculatedRange);
    //console.log( calculatedRange.length)
    //console.log(searchData)
    const matchingDates  = searchData.filter(date => date.dates.some(date => calculatedRange.includes(date)));

    //console.log(matchingDates );
    setAvailableTrainer(matchingDates)
   // setAvailableTrainer()


  };

  const getDatesInRange = (startDate, endDate = null) => {
    if (!startDate) return [];
    const start = new Date(startDate);
    const end = endDate ? new Date(endDate) : start;
     // Validate that dates are in the correct order
  if (isNaN(start) || isNaN(end) || start > end) {
    console.error("Invalid date range provided");
    return [];
  }
    const dates = [];
    const currentDate = new Date(start); // Create a copy to avoid mutating the original `start`

    while (currentDate  <= end) {
      dates.push(currentDate.toISOString().split("T")[0]); // Format as YYYY-MM-DD
    currentDate.setDate(currentDate.getDate() + 1); // Increment date
    }

    return dates;
  };

  const MyContainer = ({ className, children }) => {
    return (
      <div style={{ padding: "0px", background: "#fff", color: "#fff" }}>
        <div className={className}>
          {/* <div style={{ background: "#f0f0f0" }}>
            What is your favorite day?
          </div> */}
          <div style={{ position: "relative" }}>{children}</div>
        </div>
      </div>
    );
  };


// datepicker
//clear filter
const clearFilter=()=>{
  setTimeout(()=>{ //this function is for clearing the array and put old listing array 
    setAvailableTrainer(searchData)
  },300)
  setAvailableTrainer([])
  setTimeFilter('')
  setDateFilter('')
  setStartDate(null)
  setEndDate(null)
  setNoOfDays('7 days')
}
//clear filter
useEffect(()=>{
  setAvailableTrainer(searchData)
  
 // console.log(availableTrainer)
 
},[searchData])

  return (
    <>
      <PageTopBanner>
        <Container>
          <Row>
            <Col lg={12}>
              {/* Display search term inside double quotes */}
              {/* {searchStatus ? (
                <h1>
                  {searchData.length} result found with "{searchTerm}"
                </h1>
              ) : (
                <h1>
                  No result found{" "}
                  {searchStatus == "undefined" ? "" : `with "${searchTerm}"`}{" "}
                </h1>
              )} */}

              {/* <h1>{searchData.length} result found with "{searchTerm}"</h1> */}
              <div className="PageBanner__formWrp mt-5">
                <div className="PageBanner__formWrpTop">
                  <h2>Find your instructor now!</h2>
                </div>
                {/* Pass the search term to the SearchForm for displaying inside input */}
                <SearchForm />
              </div>
            </Col>
          </Row>
        </Container>
      </PageTopBanner>

      {searchStatus ? (
        ""
      ) : (
        <div className="bg-white">
          <WhyChooseUs />
        </div>
      )}

      <div className={`bg-grey profileCard ${searchStatus ? "" : "titleCenter"}`} >
        <Container>
          <Row>

            
          

            <Col lg={12}>
            {!searchStatus &&  
             <> <SecIntro
                titleStart={introData.titleStart}
                highlight={introData.highlight}
              />
              <DescriptionBlock content={contentData.bottomDesc} />
              </>
            }

            {searchStatus &&
            <>
             <div className="secIntro">
             <div className="secIntro__left">
               <h2>
               Instructors available in <span> {noOfDays}</span>
               </h2>
             </div>
             
             
           </div>
           <div className="Faq__Desc commonDesc">
            <p>We have {uniqueTrainer.length} available drivers for your selected date and time</p>
            </div>
           </>
            }

              
              {searchStatus ? 
              <div className="profileCard__filter d-flex justify-content-between">
                <div className="profileCard__filterLeft d-flex">
                    <div className="profileCard__filterGroup d-flex gap-3 align-items-center">
                      <strong>Time</strong>
                      <div className="d-flex gap-1">
                        <div className="d-flex profileCard__filterRadio">
                          <input type="radio" name="timeFilter" id="" checked={timeFilter === 'Anytime' ? true : false} value="Anytime" onChange={changeTime}/>
                          <div className="profileCard__filterRadioBtn">Anytime</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio">
                          <input type="radio" name="timeFilter" id="" checked={timeFilter === 'AM' ? true : false} value="AM" onChange={changeTime}/>
                          <div className="profileCard__filterRadioBtn">AM</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio">
                          <input type="radio" name="timeFilter" id="" checked={timeFilter === 'PM' ? true : false} value="PM" onChange={changeTime}/>
                          <div className="profileCard__filterRadioBtn">PM</div>
                        </div>
                      </div>
                    </div>
                    <div className="profileCard__filterGroup d-flex gap-3 align-items-center">
                      <strong>Day</strong>
                      <div className="d-flex gap-1">
                        <div className="d-flex profileCard__filterRadio d-none">
                          <input type="radio" name="dayFilter" id="" checked={dateFilter === '' ? true : false} onChange={changeDay} value=""/>
                          <div className="profileCard__filterRadioBtn" >Everyday</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio">
                          <input type="radio" name="dayFilter" id="" checked={dateFilter === 'Fri-Sun' ? true : false} onChange={changeDay} value="Fri-Sun"/>
                          <div className="profileCard__filterRadioBtn">Fri -Sun</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio">
                          <input type="radio" name="dayFilter" id="" checked={dateFilter === 'Mon-Thu' ? true : false} onChange={changeDay} value="Mon-Thu"/>
                          <div className="profileCard__filterRadioBtn">Mon- Thu</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio d-none">
                          <input type="radio" name="dayFilter" id="" />
                          <div className="profileCard__filterRadioBtn">Custom</div>
                        </div>
                        <div className="d-flex profileCard__filterRadio">
                        <DatePicker 
                        selected={startDate}
                         placeholderText="Custome ˅" 
                          onChange={onChangeDate} startDate={startDate} endDate={endDate} selectsRange 
                         // isClearable 
                          
                          dateFormat="dd-MMM"
                          shouldCloseOnSelect={true}
                           minDate={new Date()}
                           calendarContainer={MyContainer}
                          // excludeDates={[addDays(new Date(), 1), addDays(new Date(), 5)]}
                          />
                        </div>
                          
                      </div>
                    </div>
                </div>
                <button className="btn btn-blue-outline btn-sm profileCard__filterClear" onClick={clearFilter}>Clear Filter</button>
              </div>
              : ""}
              <ProfileCards 
              //  profileData={searchData} 
              profileData={availableTrainer} 
               />
            </Col>
          </Row>
        </Container>
      </div>

      {searchStatus ? (
        ""
      ) : (
        <div className="bg-white">
          <HomeFaq />
        </div>
      )}
    </>
  );
}

export default Search;
