import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import manualIco from "../assets/images/manual_transmission.svg";
import autoIco from "../assets/images/Automatic_transmission.svg";
import { apiPost } from "../utilities/axiosInstance";
import API_ENDPOINTS from "../utilities/apiEndPoints";
import { useAuth } from "../context/AuthContext";

function SearchForm() {
  const [isChecked, setIsChecked] = useState(true);
  const [inputValue, setInputValue] = useState("");
  const [postCode, setPostCode] = useState('');
  const [suggestions, setSuggestions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [noResults, setNoResults] = useState(false);
  const [isValidInput, setIsValidInput] = useState(true);
  const [isSuggestionSelected, setIsSuggestionSelected] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const { filtersuburb, setFilterSuburb, setFilterState, setFilterPostcode, setFilterSuburbID, setSelectSearchValue, setFilterStateID } = useAuth();

  useEffect(() => {
    if (location.state?.searchTerm) {
     // setInputValue(location.state.searchTerm); // this is commented because it is showing search input in search result page
    }
  }, [location.state?.searchTerm]);

  useEffect(() => {
    if (!isSuggestionSelected && inputValue.trim() !== "") {
      const delayDebounceFn = setTimeout(() => {
        fetchSuggestions(inputValue);
      }, 500);

      return () => clearTimeout(delayDebounceFn);
    }
  }, [inputValue, isSuggestionSelected]);

  const validateInput = (value) => /^[a-zA-Z0-9\s]*$/.test(value);

  const handleInputChange = (e) => {
    const value = e.target.value;
    // console.log(validateInput(value), value)
      setInputValue(value);
      setIsValidInput(true);
      setIsSuggestionSelected(false);
      if(e.target.value.length == 0){
        setNoResults(false)
        setLoading(false)
        setSuggestions([]);
      }
  };

  const fetchSuggestions = async (query) => {
    setLoading(true);
    setNoResults(false);

    try {
      const response = await apiPost(API_ENDPOINTS.FIND_SUBURBS, {
        filter: query,
      });

      if (response.success && response.data) {
        const filteredSuggestions = response.data.filter((item) =>
          [item.suburb, item.stateCode, item.postcode.toString()].some((field) =>
            field.toLowerCase().includes(query.toLowerCase())
          )
        );

        setNoResults(filteredSuggestions.length === 0);
        setSuggestions(filteredSuggestions);
      } else {
        setSuggestions([]);
        setNoResults(true);
      }
    } catch (error) {
      console.error("Error fetching suggestions:", error);
      setSuggestions([]);
      setNoResults(true);
    } finally {
      setLoading(false);
    }
  };

  const handleSuggestionClick = (suggestion) => {
    setInputValue(`${suggestion.suburb}, ${suggestion.stateCode} (${suggestion.postcode})`);
   // setInputValue(suggestion.postcode);
    setPostCode(suggestion.postcode)// for adding the post code on select event and send to api
    setIsSuggestionSelected(true);
    setSuggestions([]);

    setFilterSuburb(suggestion.suburb)
    setFilterState(suggestion.stateCode)
    setFilterPostcode(suggestion.postcode)
    setFilterSuburbID(suggestion.id)
    setFilterStateID(suggestion.state_id)
    setSelectSearchValue(true)
  };

  const handleSearchSubmit = async (e) => {
    e.preventDefault();

    if (!isSuggestionSelected) {
      toast.error("Please select a location from suggestions", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
      return;
    }

    // Determine transmission type
    const transmissionType = isChecked ? "auto" : "manual";

    // Data to be sent to the backend
    const data = {
     // location: inputValue,
      postcode: `${postCode}`,
      transmissionType: transmissionType,
      suburb:filtersuburb,  //for testing filter list by poonam
    };

    // Log the data to console
   // console.log("Submitted Data:", data);

    try {
      //const response = await apiPost(API_ENDPOINTS.INSTRUCTOR_LIST, data);
      const response = await apiPost(API_ENDPOINTS.INSTRUCTOR_LIST, data);
   //   if (response.success) {
        toast.success("Search submitted successfully!", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
        });
        // Redirect to the search results page
        setTimeout(()=>{
       navigate("/search", { state: { searchTerm: inputValue, searchResult:  response.data, searchStatus:response.success} });
       setNoResults(false);
       setInputValue();
       //console.log(inputValue, noResults)
      
        },1000)
       // navigate("/search", { state: { searchTerm: inputValue } });
      // } else {
      //   toast.error("Failed to submit search. Please try again.", {
      //     position: "top-right",
      //     autoClose: 3000,
      //     hideProgressBar: false,
      //     closeOnClick: true,
      //     pauseOnHover: true,
      //     draggable: true,
      //   });
     // }
    } catch (error) {
      console.error("Error submitting search:", error);
      toast.error("An error occurred while submitting your search.", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
    }
  };

  const handleToggle = () => {
    setIsChecked(!isChecked);
    if (isSuggestionSelected) {
      toast("Please click on search button to change your selected Transmission type", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
      });
      return;
    }
   // handleSearchSubmit(e)
  };

  useEffect(() => {
   // console.log(inputValue, noResults); // This will log updated values whenever they change
}, [inputValue, noResults]);

  return (
    <form className="SearchForm" onSubmit={handleSearchSubmit}>
      <div className="PageBanner__formWrpBottom">
        <div className="toggle-switch">
          <input
            type="checkbox"
            id="toggle-switch"
            checked={isChecked}
            onChange={handleToggle}
          />
          <label htmlFor="toggle-switch" className="slider">
            <span className="label-manual">
              <img src={manualIco} alt="Manual" /> Manual
            </span>
            <span className="label-auto">
              <img src={autoIco} alt="Auto" /> Automatic
            </span>
          </label>
        </div>

        <div className="inputWrp position-relative">
          <input
            type="text"
            placeholder="Enter your Suburb here..."
            value={inputValue}
            onChange={handleInputChange}
          />

          <ul className="suggestions-list">
            {loading && <li>Loading...</li>}
            {noResults && !loading && (
              <li>No results found for "{inputValue}"</li>
            )}

            {suggestions.length > 0 && !loading && (
              suggestions.map((suggestion, index) => (
                <li
                  key={index}
                  onClick={() => handleSuggestionClick(suggestion)}
                  style={{ cursor: "pointer" }}
                >
                  <div>
                    <strong>{suggestion.suburb}</strong>, {suggestion.stateCode}{" "}
                    ({suggestion.postcode})
                  </div>
                </li>
              ))
            )}
          </ul>
        </div>

        <button
          type="submit"
          className="btn btn-white"
          disabled={!isSuggestionSelected}
        >
          Search
        </button>
      </div>
      <ToastContainer />
    </form>
  );
}

export default SearchForm;
